$color1: #d73f09;

@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800&family=Playfair+Display:wght@700;800;900&display=swap');

.buildings-on-campus-page-outer-container {
    font-family: 'Playfair Display', serif;
    font-weight: bold;
    background: linear-gradient(
        to right bottom,
        rgba(255, 255, 255, 0.7),
        rgba(255, 255, 255, .4)
    );
    z-index: 400;
}


.buildings-on-campus-page-inner-container {}

.buildings-on-campus-hero-outer-container {}

.buildings-on-campus-hero-inner-container {
    display: flex;

}

.buildings-on-campus-hero-calling {
    font-size: 4rem;
    margin-top: 5rem;
    line-height: 1.35;
    margin-bottom: 1.35rem;
}

.buildings-on-campus-hero-statement {
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size: 1.075rem;
    max-width: 70%;
    line-height: 1.8;
    letter-spacing: .7px;
}

.buildings-on-campus-hero-add-form {
    margin-top: 5rem;
    padding: 1rem;
    border-radius: 6px;
    height: fit-content;
}

.buildings-on-campus-outer-container {}

.buildings-on-campus-inner-container {
    width: 80%;
    margin: 0 auto;
}

.buildings-on-campus-collection-title {
    color: $color1;
    text-align: center;
    font-size: 3rem;
    margin-bottom: 3rem;
}

.buildings-on-campus-collection-container {
    width: 60%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.building-on-campus {
    width: 45%;
    text-align: center;
    box-shadow: #f0f0f9 1px 1px 15px;
    margin-bottom: 3rem;
    padding: 1rem;
}

.building-on-campus:hover {
    cursor: pointer;
}

.building-on-campus-name {
    color: $color1;
    font-size: 1.5rem;
    margin-bottom: .35rem;
}

.building-on-campus-capacity {
    font-size: 1rem;
}



