$color1: #d73f09;

.course-outer-container {
    background-color: #fff;
    min-height: 100vh;
}

.course-inner-container {
    width: 80%;
    margin: 5rem auto 0;
    display: flex;
}

.course-overview-container {
    width: 50%;
}

.course-title {
    color: $color1;
    font-size: 2rem;
    margin-bottom: .35rem;
    max-width: 100%;
}

.course-teacher {
    font-size: 1.1rem;
    margin-bottom: 1rem;
}

.course-description {
    text-align: left;
}

.course-action {
    width: 50%;
    padding: 0 3rem;
}

.course-action-options {
    display: flex;
    margin-bottom: 2rem;
}

.course-action-option {
    width: 100%;
    text-align: center;
    padding: 1rem 0;
    font-weight: bold;
}

.course-action-option:hover {
    cursor: pointer;
}

.course-action-option-active {
    background-color: #d73f09;
    color: #ffffff;
}

.course-update-label {
    color: $color1;
    margin-bottom: .35rem;
}

.course-update-input {
    background-color: transparent;
    border: none;
    border-bottom-style: solid;
    border-bottom-color: $color1;
    border-bottom-width: 1px;
    padding: .35rem 0;
    margin-bottom: 1.5rem;
    width: 100%;
}

.course-update-dropdown {
    background-color: transparent;
    border: none;
    margin-top: .35rem;
    margin-bottom: 1.5rem;
}

.course-update-textarea {
    width: 100%;
    min-height: 20vh;
    border: none;
    box-shadow: #dedede 0px 0px 6px;
    padding: 6px;
    margin: .7rem 0 1.5rem;
    font-size: .75rem;
    font-family: "Montserrat";
    resize: none;
}

.course-update-btn {
    width: 100%;
    background-color: $color1;
    color: #fff;
    border: none;
    font-size: 1.35rem;
    font-weight: bold;
    padding: 1rem 0;
}

.course-update-btn:hover {
    cursor: pointer;
}

.course-delete-container {}

.course-delete-warning {}

.course-delete-btn {
    background-color: $color1;
    color: #fff;
    border: none;
    font-weight: bold;
    font-size: 1.35rem;
    padding: 1rem 0;
    width: 100%;
    margin: 1rem 0 0;
}

.course-delete-btn:hover {
    cursor: pointer;
}