@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800&family=Playfair+Display:wght@700;800;900&display=swap');

h1 {
    color: #d3322a;
}

.home-page-outer-container {
    font-family: 'Playfair Display', serif;
    font-weight: bold;
    background: linear-gradient(
        to right bottom,
        rgba(255, 255, 255, 0.7),
        rgba(255, 255, 255, .4)
    );
    z-index: 400;
}


.home-page-inner-container {}

.home-hero-outer-container {}

.home-hero-inner-container {}

.home-hero-calling {
    font-size: 4rem;
    margin-top: 5rem;
    line-height: 1.35;
    margin-bottom: 1.35rem;
}

.home-hero-statement {
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size: 1.075rem;
    max-width: 50%;
    line-height: 1.8;
    letter-spacing: .7px;
}

.home-page-hero-illustration {
    position: absolute;
    width: 80%;
    top: 20vh;
    right: -22vh;
}

@media (min-width: 1080px) {
    .home-hero-inner-container {
        max-width: 80%;
        margin: 0 auto;
        min-height: 90vh;
    }
}